.modal
{
	&-header
	{
		border-color: #ccc;
		h4
		{
			font-size: 16px;
		}
	}
	&-content
	{
		border-radius: 10px;
		border: none;
		box-shadow: 0 0 10px rgba(0,0,0,0.35);
		background-color: $bg;
	}
	&-body
	{
		padding: 15px;
		.form-control
		{
			color: $gray
		}
	}
	&-footer
	{
		border-color: #000;
	}
	[data-dismiss]
	{
	    @include absolute(10px,f,f,10px);
	    width: 30px;
	    height: 30px;
	    cursor: pointer;
	    z-index: 9999;
	    &:before
	    {
		    @include absolute(15px,f,f,-5px);
		    content: '';
		    transform: rotate(45deg);
		    width: 40px;
		    height: 1px;
		    background: #555;
		}
		&:after
		{
			@include absolute(15px,f,f,-5px);
		    content: '';
		    transform: rotate(-45deg);
		    width: 40px;
		    height: 1px;
		    background: #555;
		}
	}
}