.ads-content
{
    z-index: 50;
    margin-bottom: 80px;
    position: relative;
    &__header
    {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        padding: 20px 0;
        border-bottom: 1px solid #eee;
        &__logo
        {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100px;
            width: 100px;
            flex: 0 0 100px;
            max-width: 100px;
            margin-left: 20px;
            img
            {
                max-width: 100%;
            }
        }
        &__name
        {
            h1
            {
                font-size: 18px;
                font-weight: 600;
                color: #333;
                margin-bottom: 10px;
            }
            p
            {
                margin-bottom: 0;
                color: #777;
            }
        }
        &__social
        {
            margin-right: auto;
            ul
            {
                display: flex;
                align-items: center;
                li
                {
                    margin-left: 15px;
                    &:last-child
                    {
                        margin-left: 0;
                    }
                    a
                    {
                        font-weight: 600;
                        font-size: 20px;
                        color: #777;
                        &:hover
                        {
                            color: $color;
                        }
                    }
                }
            }
        }
    }
    &__desc
    {
        font-size: 13px;
        line-height: 35px;
        text-align: justify;
        box-shadow: 0 8px 15px -15px rgba(0, 0, 0, 0.35);
        padding-bottom: 15px;
        margin-bottom: 15px;
        p
        {
            margin-bottom: 0;
        }
    }
    &__seller
    {
        box-shadow: 0 8px 15px -15px rgba(0, 0, 0, 0.35);
        padding-bottom: 15px;
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        &__avatar
        {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            overflow: hidden;
            margin-left: 15px;
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__name
        {
            font-weight: 600;
            font-size: 13px;
            display: flex;
            small
            {
                margin-right: 5px;
            }
        }
    }
    &__price
    {
        box-shadow: 0 8px 15px -15px rgba(0, 0, 0, 0.35);
        padding-bottom: 25px;
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__old
        {
            position: relative;
            &:before
            {
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                top: 50%;
                transform: rotate(-5deg);
                height: 1px;
                background-color: #999;
            }
            span
            {
                font-weight: 600;
                line-height: 30px;
                font-size: 19px;
                color: #989898;
                display: flex;
                align-items: center;
                small
                {
                    font-size: 11px;
                    margin-right: 5px;
                    line-height: 10px;
                }
            }
        }
        &__new
        {
            span
            {
                font-weight: 600;
                line-height: 30px;
                font-size: 25px;
                color: #40ab75;
                display: flex;
                align-items: center;
                small
                {
                    font-size: 11px;
                    margin-right: 5px;
                    line-height: 10px;
                    color: #878787;
                }
            }
        }
    }
    &__list
    {
        line-height: 35px;
        font-size: 13px;
        font-weight: 600;
        margin-bottom: 15px;
        ul
        {
            column-count: 2;
            li
            {
                padding-right: 15px;
                position: relative;
                &:before
                {
                    content: '';
                    width: 8px;
                    height: 8px;
                    @include absolute(50%,0,f,f);
                    
                    transform: translateY(-50%) rotate(15deg);
                    background-color: $color;
                }
            }
        }
    }
    &__btn
    {
        display: flex;
        align-items: center;
        background-color: #f5f5f5;
        padding: 10px;
        border-radius: 15px;
        a
        {
            line-height: 55px;
            background-color: $color;
            border-radius: 10px;
            color: #fff;
            font-size: 16px;
            font-weight: 600;
            text-align: center;
            margin-right: auto;
            padding: 0 35px;
            box-shadow: 0 3px 15px rgba(0,0,0,0.15);
        }
        span
        {
            margin-right: 10px;
            font-weight: 600;
            font-size: 13px;
        }
    }
    &__slider
    {
        &__item
        {
            height: 400px;
            position: relative;
            overflow: hidden;
            border-radius: 15px;
            margin-bottom: 30px;
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    
}
@media(max-width:768px)
{
    .ads-content
    {
        margin-bottom: 30px;
        &__slider
        {
            &__item
            {
                height: 250px;
            }
        }
    }
}



@media(max-width:578px)
{
    .ads-content
    {
      
        &__seller
        {
            &__name
            {
                a
                {
                    display: flex;
                    flex-direction: column;
                    small
                    {
                        margin-top: 5px;
                    }
                }
            }
        }
        
        &__list
        {
            ul
            {
                column-count: 1;
            }
        }
        &__btn
        {
            flex-direction: column;
          justify-content: center;
          a
          {
              margin-right: 0;
              margin-top: 20px;
              width: 100%;
          }

        }
    }
}