.cats
{
    margin-bottom: 40px; 
    &__item
	{
		background-color: $bg;
		border-radius: 15px;
		box-shadow:0 25px 45px -25px rgba(0, 0, 0, 0.25);
        margin-bottom: 30px;
        height: 200px;
        position: relative;
        overflow: hidden;
        $item: &;
		&__image
		{
            @include absolute(0,0,0,0);
            height: 100%;
            z-index: 1;
			img
			{
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: 0.4s;
			}
		}
		&__title
		{
            @include absolute(f,0,0,0);
            z-index: 2;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(234,172,28,0.85);
            border-radius: 50% 50% 0 0;
            transition: 0.4s;
            height: 40px;
			h2
			{
				font-size: 14px;
                font-weight: 600;
                line-height: 40px;
                transition: 0.4s;
			}
        }
        >a
        {
            @include absolute(0,0,0,0);
            z-index: 3;
            
        }
        &:hover
        {
            #{$item}__image
            {
                img
                {
                    filter: blur(5px);
                    transform: scale(1.1);
                }
            }
            #{$item}__title
            {
                height: 100%;
                border-radius: 0;
                background-color: rgba(0,0,0,0.55);
                h2
                {
                    color: #fff;
                    font-size: 18px;
                }
            }
        }
	}
}

@media(max-width:768px)
{
    .cats
    {
        margin-bottom: 0px;
    }
}
@media(max-width:576px)
{
    .cats
    {
        margin-bottom: 0px;
        &__item
        {
            height: 170px;
            margin-bottom: 20px;
            box-shadow:0 25px 25px -25px rgba(0, 0, 0, 0.25);
            $item: &;
            &:hover
            {
                #{$item}__image
                {
                    img
                    {
                        filter: blur(0);
                        transform: unset;
                    }
                }
                #{$item}__title
                {
                    height: 40px;
                    background-color: rgba(234,172,28,0.85);
                    border-radius: 50% 50% 0 0;
                    h2
                    {
                        color: #222;
                        font-size: 14px;
                    }
                }
            }
        }
    }
}