.salon-slider
{
    margin-bottom: 50px;
    overflow: hidden;
    .swiper
    {
        &-container
        {
            position: relative;
            overflow: visible;
            padding: 0 0 55px;
        }
        &-slide
        {
            opacity: 0.2;
            &-active
            {
                opacity: 1;
                + .swiper-slide,
                + .swiper-slide + .swiper-slide,
                + .swiper-slide + .swiper-slide + .swiper-slide,
                {
                    opacity: 1;
                }
            }
        }
        &-pagination
        {
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 20px;
            &-bullet
            {
                transition: 0.2s;
                border-radius: 4px;
                &-active
                {
                    width: 30px;
                    background-color: $color;
                }
            }
        }
        &-btn
        {
            @include absolute(175px,-45px,f,f);
            transform: translateY(-50%);
            z-index: 20;
            font-size: 14px;
            transition: 0.3s;
            width: 35px;
            height: 35px;
            line-height: 35px;
            border-radius: 22.5px;
            background-color: #e3e3e3;
            color: #333;
            opacity: 0.75;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            box-shadow: 0 0 10px rgba(0,0,0,0.1);
            &:active,&:focus
            {
                outline: none;
            }
            &:hover
            {
                opacity: 1;
            }
            cursor: pointer;
            i
            {
                line-height: inherit;
            }
            &-next
            {
                right: unset;
                left: -45px;
            }
        }
        &-button
        {
            &-disabled
            {
                opacity: 0.15;
                cursor: default;
                &:hover
                {
                    opacity: 0.15;
                }
            }
        }
    }
    &__item
    {
        box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.1);
        background-color: #fff;
        border-radius: 5px;
        padding-bottom: 15px;
        overflow: hidden;
        transition: 0.3s;
        $item: &;
        &__image
        {
            border-radius: 5px 5px 0 0;
            height: 150px;
            margin-bottom: 10px;
            transition: 0.2s;
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__title
        {
            padding: 0 15px 10px;
            margin-bottom: 15px;
            box-shadow: 0 3px 15px -10px rgba(0,0,0,0.35);
            transition: 0.2s;
            h2
            {
                font-size: 13px;
                font-weight: 600;
                line-height: 30px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }
            a
            {
                transition: 0.3s;
            }
        }
        &__desc
        {
            padding: 0 15px 15px;
            color: #979ba9;
            text-align: justify; 
            line-height: 25px;
            margin-bottom: 15px;
            box-shadow: 0 3px 15px -10px rgba(0,0,0,0.35);
            p
            {
				margin-bottom: 0;
				height: 50px;
				overflow: hidden;
				position: relative;
            }
        }
        &__row
        {
            position: relative;
            padding: 0 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &__author
        {
            display: flex;
			align-items: center;
			max-width: 50%;
			flex: 0 0 50%;
            &__avatar
            {
				flex: 0 0 25px;
				max-width: 25px;
                width: 25px;
                height: 25px;
                border-radius: 50%;
                margin-left: 10px;
                img
                {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 50%;
                }
            }
            &__name
            {
				flex: 0 0 calc(100% - 25px);
				max-width: calc(100% - 25px);
                line-height: 25px;
                font-weight: 600;
                a
                {
					color: #878787;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					display: block;
                }
            }
        }
        &__price
        {
            display: flex;
            align-items: flex-end;
            flex-direction: column;
            &__old
            {
                font-size: 13px;
                position: relative;
                line-height: 15px;
                &:before
                {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 45%;
                    transform: rotate(-5deg);
                    height: 1px;
                    background-color: #999;
                }
                span
                {
                    display: flex;
                    color: #989898;
                }
                small
                {
                    font-size: 10px;
                    margin-left: 5px;
                    line-height: 10px;
                }
            }
            &__new
            {
                font-size: 17px;
                color: #40ab75;
                font-weight: 600;
                span
                {
                    line-height: 25px;
                    display: flex;
                    align-items: center;
                    flex-direction: row-reverse;
                }
                small
                {
                    font-size: 11px;
                    margin-left: 5px;
                    line-height: 10px;
                    color: #878787;
                }
            }
        }
        &__more
        {
            height: 175px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: 0.2s;
            span
            {
                display: flex;
                align-items: center;
                flex-direction: row-reverse;
                font-weight: 600;
                color: #40ab75;
                font-size: 14px;
                i
                {
                    margin-right: 20px;
                    color: #999;
                }
            }
        }
        &--more
        {
            #{$item}__image
            {
                position: relative;
                overflow: hidden;
                opacity: 0.5;
                img
                {
                    filter: blur(5px)
                }
            }
            >a
            {
                @include absolute(0,0,0,0);
                z-index: 5;
            }
        }
        &:hover
        {
            box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
            #{$item}__image
            {
                height: 140px;
            }
            #{$item}__more
            {
                height: 185px;
            }
            #{$item}__title
            {
                padding: 5px 15px 15px;
                a
                {
                    color: $color;
                }
            }
        }
    }
}
@media(max-width:1280px)
{
    .salon-slider
    {
        .swiper
        {
            &-btn
            {
                &-prev
                {
                    right: -5px;
                }
                &-next
                {
                    left: -5px;
                }
            }
        }
    }
}
@media(max-width:768px)
{
    .salon-slider
    {
        margin-bottom: 5px;
    }
}
@media(max-width:576px)
{
    .salon-slider
    {
        margin-bottom: 5px;
        &__item
        {
            $item: &;
            &:hover
            {
                box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
                #{$item}__image
                {
                    height: 150px;
                }
                #{$item}__title
                {
                    padding: 0 15px 10px;
                    a
                    {
                        color: #000;
                    }
                }
            }
        }
    }
}