.sec-title
{
	margin-bottom: 30px;
	position: relative;
	overflow: hidden;
	display: flex;
	align-items: center;
	&:before
	{
		content: '';
		@include absolute(50%,0,f,0);
		height: 1px;
		background-color: #eee;
		z-index: 1;
	}
	h2,h3,h4,h5,h6
	{
		line-height: 40px;
		font-weight: 300;
		font-size: 20px;
		color: #222;
		position: relative;
		z-index: 2;
		background-color: $bg;
		padding-left: 15px;
		display: flex;
		align-items: center;
		i
		{
			margin-left: 10px;
			color: $color;
			font-size: 15px;
		}
	}
	a
	{
		margin-right: auto;
		position: relative;
		z-index: 2;
		padding-right: 15px;
		background-color: $bg;
		color: #777;
		font-weight: 600;
		transition: 0.3s;
		&:hover
		{
			color: $color;
		}
	}
}
.inner-title
{
	line-height: 40px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 50px;
	&:before {
		content: '';
		@include absolute(50%,0,f,0);
		height: 1px;
		background-color: #eee;
		z-index: 1;
	}

	h2,h3,h4,h5,h6 {
		padding: 0 15px;
		background-color: $bg;
		line-height: inherit;
		z-index: 2;
		font-size: 18px;
		font-weight: 500;
		position: relative;
	}
}


@media (max-width: 768px) {
	.inner-title,.sec-title
	{
		margin-bottom: 20px;
		h2,h3,h4,h5,h6
		{
			font-size: 18px;
			line-height: 30px;
		}
	}
}
@media(max-width:480px)
{
	.inner-title,.sec-title
	{
		margin-bottom: 20px;
		h2,h3,h4,h5,h6
		{
			font-size: 14px;
			line-height: 30px;
			font-weight: 500;
		}
	}
}
