.profile
{
	padding: 15px 0;
	$self: &;
	&__header
	{
		border-radius: 10px;
		display: flex;
		align-items: center;
		padding: 10px 15px;
		margin-bottom: 10px;
		background-image: linear-gradient(15deg, $g1, $g5);
		&__toggle
		{
			margin-left: 20px;
			color: #fff;
			display: none;
			i
			{
				font-size: 20px;
				cursor: pointer;
			}
		}
		&__user
		{
			display: flex;
			align-items: center;
			&__avatar
			{
				width: 40px;
				height: 40px;
				margin-left: 15px;
				img
				{
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 50%;
				}
			}
			&__name
			{
				span
				{
					color: #fff;
					font-size: 13px;
					font-weight: 600;
				}
			}
		}
		&__menu
		{
			margin-right: auto;
			ul
			{
				li
				{
					a
					{
						font-weight: 600;
						font-size: 13px;
						padding: 0 25px;
						background-color: #fff;
						border-radius: 5px;
						line-height: 30px;
						display: flex;
					}
				}
			}
		}
	}
	&__wrapper
	{

		border: 1px solid #eee;
		border-radius: 10px;
		display: flex;
		position: relative;
		overflow: hidden;
	}
	&__sidebar
	{
		flex: 0 0 220px;
		max-width: 220px;
		transition: 0.2s;
		nav
		{
			transition: 0.2s;
			height: 100%;
			box-shadow: -10px 0 10px -5px rgba(0,0,0,0.05);
			>ul
			{
				>li
				{
					border-bottom: 1px solid rgba(0,0,0,0.05);
					position: relative;
					>i
					{
						@include absolute(25px,f,f,15px);
						transform: translateY(-50%);
						font-size: 10px;
						z-index: 1;
						transition: 0.2s;
					}
					>a
					{
						line-height: 50px;
						padding: 0 15px;
						display: flex;
						align-items: center;
						font-weight: 600;
						font-size: 14px;
						position: relative;
						color: #333;
						>i
						{
							margin-left: 12px;
							line-height: inherit;
							font-size: 16px;
						}
						>img
						{
							margin-left: 12px;
							height: 22px;
						}
						&:before
						{
							content: '';
							@include absolute(8px,0,8px,f);
							width: 3px;
							border-radius: 3px 0 0 3px;
							background-color: transparent;
						}
						span
						{
							display: flex;
							align-items: center;
							flex-direction: row-reverse;
							text-overflow: ellipsis;
							overflow: hidden;
							white-space: nowrap;
							small
							{
								margin-right: 10px;
								line-height: 15px;
								background-color: $g1;
								padding: 0 5px;
								border-radius: 7.5px;
								color: #fff;
							}
						}
					}
					&:hover
					{
						>a
						{
							color: #000;
						}
					}
					&.active
					{
						>i
						{
							transform: translateY(-50%) rotateX(180deg);
						}
						>a
						{
							color: #000;
							&:before
							{
								background-color: $g3;
							}
							>i
							{
								color: rgba(0,0,0,.6);
							}
						}
						&:hover
						{
							>a
							{
								background-color: #fff;
							}
						}
					}
					>ul
					{
						border-top: 1px solid #eee;
						background-color: #fcfcfc;
						display: none;
						>li
						{
							border-bottom: 1px solid #eee;
							>a
							{
								line-height: 40px;
								padding: 0 15px;
								display: flex;
								align-items: center;
								font-weight: 500;
								font-size: 13px;
								position: relative;
								>i
								{
									margin-left: 12px;
									line-height: inherit;
									font-size: 16px;
								}
								>img
								{
									margin-left: 12px;
									height: 20px;
								}
								span
								{
									display: flex;
									align-items: center;
									flex-direction: row-reverse;
									small
									{
										margin-right: 10px;
										line-height: 15px;
										background-color: $g1;
										padding: 0 5px;
										border-radius: 7.5px;
										color: #fff;
									}
								}
							}
							&:last-child
							{
								border-bottom: none;
							}
						}
					}
				}
			}
		}
	}
	&__content
	{
		flex: 0 0 calc(100% - 220px);
		max-width: calc(100% - 220px);
		transition: 0.2s;
		padding: 25px;

		&__filter
		{
			border-bottom: 1px solid #eee;
			padding-bottom: 5px;
			margin-bottom: 15px;
			&__wrapper
			{
				display: flex;
				margin: 0 -5px;
				>div
				{
					width: 20%;
					padding: 0 5px;
					margin-bottom: 10px;
					&:last-child
					{
						width: 10%;
						margin-right: auto;
					}
				}
				input
				{
					width: 100%;
					padding: 0 10px;
					line-height: 35px;
					background-color: #fafafa;
					border-radius: 5px;
					border: none;
					color: #222;
					&:focus
					{
						background-color: #f6f6f6;
					}
				}
				button
				{
					padding: 0 5px;
					background-color: $g1;
					color: #fff;
					border-radius: 5px;
					border: none;
					line-height: 35px;
					width: 100%;
					font-size: 14px;
				}

				.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn)
				{
					width: 100%;
					> .dropdown-toggle
					{
						text-align: right;
						padding: 0 10px;
						line-height: 35px;
						background-color: #fafafa;
						border-radius: 5px;
						color: #222;
					}
					&:focus,&:hover
					{
						background-color: #f6f6f6;
						border-radius: 5px;
					}
				}
			}
		}
		&__header
		{
			border-bottom: 1px solid #eee;
			padding-bottom: 15px;
			margin-bottom: 15px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			>span
			{
				font-weight: 600;
				font-size: 16px;
				display: flex;
				align-items: center;
				img
				{
					height: 25px;
					margin-left: 10px;
				}
			}
			div
			{
				display: flex;
				align-items: center;
				flex-direction: row-reverse;
				a
				{
					padding: 0 25px;
					background-color: $g1;
					color: #fff;
					border-radius: 5px;
					border: none;
					line-height: 30px;
					font-size: 13px;
					margin-right: 5px;
				}
				span
				{
					color: #777;
				}
			}
		}
		&__footer
		{

		}
		&__dashboard
		{
			&__item
			{
				// height: 90px;
				border-radius: 15px;
				box-shadow: 0 0 10px rgba(0,0,0,0.1);
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 0 20px;
				margin-bottom: 25px;
				span
				{
					font-size: 15px;
					font-weight: 600;
					&:last-child{
						font-size: 25px;
						color: $color;
					}
				}
			}
			&__chart
			{
				height: 300px;
				canvas
				{
					height: 100%;
				}
			}
		}
		&__table
		{
			table
			{
				width: 100%;
				thead
				{
					tr
					{
						th
						{
							line-height: 40px;
							text-align: center;
							border-bottom: 1px solid #ddd;
							font-weight: 600;
							font-size: 13px;
						}
					}
				}
				tbody
				{
					tr
					{
						&:hover
						{
							td
							{
								background-color: #f5f5f5;
							}
						}

						td
						{
							line-height: 50px;
							text-align: center;
							font-size: 13px;
							vertical-align: middle;
							padding: 10px 5px;
							&:focus
							{
								outline: none;
							}
							a
							{
								line-height: 23px;
								padding: 0 15px;
								background-color: #fff;
								border-radius: 5px;
								font-size: 12px;
								font-weight: 600;
								color: #666;
								border: 1px solid #666;
								transition: 0.2s;
								cursor: pointer;
								&:hover
								{
									background-color: #666;
									color: #fff;
								}
								&.green
								{
									border-color: $g1;
									color: $g1;
									&:hover
									{
										background-color: $g1;
										color: #fff;
									}
								}
								&.red
								{
									border-color: $red;
									color: $red;
									&:hover
									{
										background-color: $red;
										color: #fff;
									}
								}
								&.orange
								{
									border-color: $purple;
									color: $purple;
									&:hover
									{
										background-color: $purple;
										color: #fff;
									}
								}
							}
							&.td-wallet-iconic-title
							{
								text-align: right;
								font-size: 14px;
								font-weight: 600;
								line-height: 50px;
								i
								{
									float: right;
									font-size: 35px;
									margin-left: 15px;
									line-height: inherit;
								}
								span
								{
									display: flex;
									flex-direction: column;
									line-height: 30px;
									small
									{
										line-height: 20px;
										direction: ltr;
										font-family: SFMono-Regular,Menlo,Monaco,Consolas,Liberation Mono,Courier New,monospace;
									}
								}
							}
							&.td-wallet-balance
							{
								span
								{
									display: block;
									line-height: 25px;
									font-weight: 600;
									font-size: 12px;
									&:first-child
									{
										direction: ltr;
									}
								}
							}
							&.td-wallet-action
							{
								width: 400px;
							}
						}
						&:nth-child(even)
						{
							background-color: #fafafa
						}
					}
				}
			}
		}
		&__wallets
		{
			&__search
			{
				margin-bottom: 15px;
			}
			&__list
			{
				padding: 15px;
			    background-color: #efefef;
			    border-radius: 10px;
			}
			&__item
			{
			    padding: 15px;
			    background-color: #fff;
			    border-radius: 5px;
			    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.075);
			    display: flex;
			    align-items: center;
			    margin-bottom: 15px;
			    &:last-child
			    {
			    	margin-bottom: 0;
			    }
			    &__image
			    {
			    	display: flex;
			    	align-items: center;
			    	justify-content: center;
			    	margin-left: 15px;
			    	i
			    	{
			    		font-size: 40px;
			    		line-height: 40px;
			    	}
			    	img
			    	{
			    		height: 40px;
			    	}
			    }
			    &__title
			    {
			    	display: flex;
			    	flex-direction: column;
			    	font-size: 16px;
			    	font-weight: 600;
			    	small
			    	{
			    		line-height: 20px;
					    direction: ltr;
					    font-family: SFMono-Regular,Menlo,Monaco,Consolas,Liberation Mono,Courier New,monospace;
			    	}
			    }
			    &__price
			    {
			    	margin-right: auto;
			    	display: flex;
			    	align-items: center;
			    	&__item
			    	{
			    		margin-left: 8px;
			    		padding: 2.5px 10px;
			    		border-radius: 5px;
			    		display: flex;
			    		flex-direction: column;
			    		border: 1px solid #eee;
			    		&:last-child
			    		{
			    			margin-left: 0;
			    		}
			    		&--green
						{
							border-color: $green; 
						}
						&--red
						{
							border-color: $red;
						}
			    		span
				    	{
				    		&:first-child
				    		{
				    			font-size: 11px;
				    			line-height: 20px;
				    			display: flex;
				    			font-weight: 600;
				    			margin-bottom: 10px;
				    			justify-content: center;
				    		}
				    		&:last-child
				    		{
				    			line-height: 20px;
							    font-weight: 500;
							    font-size: 16px;
							    white-space: nowrap;
							    display: flex;
					    		align-items: center;
					    		justify-content: center;
					    		letter-spacing: 1px;
					    		small
					    		{
					    			color: #888;
					    			font-size: 10px;
					    			margin-right: 4px;
					    			letter-spacing: 0
					    		}
				    		}
				    	}
			    	}
			    }
			    &__actions
			    {
			    	display: flex;
			    	flex-wrap: wrap;
			    	flex: 0 0 180px;
			    	max-width: 180px;
			    	margin: 0 8px -5px 0;
			    	a
			    	{
			    		flex: 0 0 calc(50% - 5px);
			    		max-width: calc(50% - 5px);
			    		margin: 0 2.5px 5px;
			    		line-height: 25px;
			    		text-align: center;
			    		font-weight: 600;
			    		border-radius: 3px;
			    		background-color: #f3f3f3;
			    		&:hover
						{
							background-color: #666;
							color: #fff;
						}
						&.green
						{
							border-color: $green; 
							color: $green;
							&:hover
							{
								background-color: $green;
								color: #fff;
							}
						}
						&.red
						{
							border-color: $red;
							color: $red;
							&:hover
							{
								background-color: $red;
								color: #fff;
							}
						}
						&.orange
						{
							border-color: $purple;
							color: $purple;
							&:hover
							{
								background-color: $purple;
								color: #fff;
							}
						}
			    	}
			    }
			    &--no-result
			    {
			    	display: none;
			    }
			}
		}
		&__verification
		{
			&__selfie
			{
				height: 154px;
				width: 100%;
			}
		}
		&__form
		{
			&__wrapper
			{
				padding: 15px;
				background-color: #efefef;
				border-radius: 10px;
				.form-group
				{
					padding: 15px;
					background-color: #fff;
					border-radius: 5px;
					box-shadow: 0 2px 3px rgba(0,0,0,0.075);
					.form-group
					{
						box-shadow: none;
						border-radius: 0;
						background-color: transparent;
						padding: 0;
						margin-bottom: 0;
					}
					&-btn
					{
						box-shadow: unset;
						padding: unset;
						border-radius: unset;
						background-color: unset;
						margin-bottom: 0;
						.btn
						{
							width: 100%;
						    background-color: #444;
						    color: #fcc201;
							font-size: 15px;
							border: none;
							border-radius: 10px;
						}
					}
					&-modal
					{
						box-shadow: none;
						border-radius: 0;
						background-color: transparent;
						padding: 0;
					}
					.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn)
					{
						width: 100%;
						> .dropdown-toggle
						{
							border: none;
							background-color: #f5f5f5
						}
					}
					.price-placeholder
					{
						text-align: center;
						font-size: 22px;
						letter-spacing: 1px;
						font-weight: 600;
						border: none;
						background-color: #f9f9f9;
						line-height: 35px;
						border-radius: 5px;
						margin: 10px 0;
					}
				}
			}
			ul
			{
				margin-bottom: 15px;
				li
				{
					display: flex;
					justify-content: space-between;
					font-size: 13px;
					line-height: 35px;
					b
					{
						font-size: 16px;
						small
						{
							margin-right: 5px;
						}
					}
				}
			}
			.select-network
			{
				display: flex;
				flex-wrap: wrap;
				margin-bottom: -10px;
				&__item
				{
					margin-left: 10px;
					margin-bottom: 10px;
					&:last-child
					{
						margin-left: 0;
					}
					label
					{
						line-height: 30px;
						padding: 0 25px;
						border-radius: 3px;
						color: #888;
						background-color: #f5f5f5;
						font-weight: 600;
						font-size: 12px;
						cursor: pointer;
						margin-bottom: 0;
					}
					input
					{
						display: none;
						&:checked
						{
							+ label
							{
								color: #fff;
								background-color: $g4;
							}
						}
					}
				}
			}
			.select-color
			{
				display: flex;
				justify-content: space-between;
				margin-bottom: 10px;
				&__item
				{
					label
					{
						line-height: 30px;
						height: 30px;
						width: 30px;
						border-radius: 3px;
						color: #888;
						background-color: #f5f5f5;
						font-weight: 600;
						font-size: 16px;
						cursor: pointer;
						margin-bottom: 0;
					}
					input
					{
						display: none;
						&:checked
						{
							+ label
							{
								box-shadow: 0 0 0 2px #fff,0 0 0 4px $g4;
							}
						}
					}
					&--red
					{
						label
						{
							background-color: #c51414
						}
					}
					&--blue
					{
						label
						{
							background-color: #3f51b5
						}
					}
					&--pink
					{
						label
						{
							background-color: #9c27b0
						}
					}
					&--green
					{
						label
						{
							background-color: #4caf50
						}
					}
					&--brown
					{
						label
						{
							background-color: #795548
						}
					}
					&--gray
					{
						label
						{
							background-color: #999
						}
					}
					&--black
					{
						label
						{
							background-color: #222
						}
					}
					&--orange
					{
						label
						{
							background-color: #ff5722
						}
					}
					&--limoon
					{
						label
						{
							background-color: #cddc39
						}
					}
					&--purple
					{
						label
						{
							background-color: #9c27b0
						}
					}
				}
			}
			.select-wallet
			{
				>ul
				{
					display: flex;
					align-items: center;
					justify-content: space-between;
					li
					{
						flex: 0 0 50%;
						max-width: 50%;
						border-bottom: 2px solid #ddd;
						line-height: 35px;
						font-size: 13px;
						font-weight: 600;
						text-align: center;
						color: #999;
						display: flex;
						justify-content: center;
						cursor: pointer;
						&.active
						{
							border-color: $g3;
							color: $g3;
						}
					}
				}
				.tab-content
				{
					display: none;
					&.active
					{
						display: block
					}
				}
			}
			.wallet
			{
				border: 1px solid #ccc;
				border-radius: 5px;
				padding: 10px;
			}
			.wallet-address
			{
				font-size: 16px;
				text-align: center;
				cursor: pointer;
				border-radius: 4px;
				display: block;
				font-family: SFMono-Regular,Menlo,Monaco,Consolas,Liberation Mono,Courier New,monospace;
				background-color: #f5f5f5;
				line-height: 30px;
				white-space: wrap;
				text-overflow: none;
			}
			.wallet-address-qr
			{
				margin: 10px auto;
				display: block;
			}
			.bank-pay
			{
				display: flex;
				flex-direction: column;
				&__item
				{
					margin-bottom: 10px;
					&:last-child
					{
						margin-bottom: 0;
					}
					label
					{
						line-height: 20px;
						padding: 15px;
						border-radius: 3px;
						background-color: #f5f5f5;
						color: #888;
						font-weight: 600;
						font-size: 13px;
						cursor: pointer;
						margin-bottom: 0;
						width: 100%;
						position: relative;
						&:before
						{
							content: '';
							@include absolute(50%,f,f,15px);
							transform: translateY(-50%);
							width: 18px;
							height: 18px;
							border-radius: 50%;
							border: 1px solid #ccc;
							transition: 0.15s;
						}

					}
					input
					{
						display: none;
						&:checked
						{
							+ label
							{
								color: #fff;
								background-color: $g4;
								&:before
								{
									content: '';
									transform: translateY(-70%) rotate(-45deg);
									width: 18px;
									height: 8px;
									border-radius: 0;
									border: 2px solid #fff;
									border-right: 0;
									border-top: 0;
								}
							}
						}
					}
				}
			}
			.bank-option
			{
				display: flex;
				align-items: center;
				font-size: 14px;
				font-weight: 600;
				i
				{
					font-size: 24px;
					margin-left: 10px;
				}
				img
				{
					margin-left: 10px;
					height: 24px;
				}
				span
				{
					span
					{
						font-size: 12px;
						font-weight: 400;
						margin-right: 5px;
					}
				}
			}
			&__timer
			{
				display: flex;
				align-items: center;
				border: 1px solid transparent;
				margin: -15px;
				padding: 15px;
				transition: 0.1s;
				border-radius: 5px;
				&.pulse
				{
					border-color: $g3;
				}
				img
				{
					width: 60px;
					flex: 0 0 60px;
					max-width: 60px;
				}
				span
				{
					font-size: 13px;
					font-weight: 600;
					line-height: 25px;
					padding-right: 10px;
					color: #666;
					flex: 0 0 calc(100% - 60px);
					max-width: calc(100% - 60px);
					text-align: center;
					b
					{
						font-size: 15px;
						color: $g3;
					}
				}
			}
		}
		&__ads
		{
			&__list
			{
				line-height: 35px;
				border-bottom: 1px solid #eee;
				padding-bottom: 15px;
				margin-bottom: 15px;
				font-weight: 600;
				ul
				{
					column-count: 2;
				}
			}
			&__desc
			{
				text-align: justify;
				line-height: 35px;
				font-size: 13px;
			}
			&__image
			{
				height: 350px;
				border-radius: 10px;
				position: relative;
				overflow: hidden;
				img
				{
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			&__chart
			{
				canvas
				{
					height: 250px;
				}
			}
		}
		&__plans
		{
			&__item
			{
				border-radius: 10px;
				overflow: hidden;
				background-color: #fff;
				box-shadow: 0 0 20px rgba(0,0,0,0.1);
				&__title
				{
					color: #333;
					padding: 20px 15px;
					text-align: center;
					span
					{
						font-size: 16px;
						font-weight: 600;
						line-height: 30px;
					}
				}
				&__price
				{
					background-color: $color;
					color: #fff;
					font-size: 24px;
					text-align: center;
					padding: 20px 15px;
					font-weight: 600;
					display: flex;
					align-items: center;
					justify-content: center;
					span
					{
						display: flex;
						align-items: center;
						align-content: center;
					}
					small
					{
						font-size: 13px;
						margin-right: 5px;
					}
				}
				&__list
				{
					
					ul
					{
						li
						{
							padding: 0 15px;
							line-height: 50px;
							display: flex;
							align-items: center;
							justify-content: space-between;
							font-size: 13px;
							&:nth-child(even)
							{
								background-color: #f5f5f5;
							}
							span
							{
								&:last-child
								{
									font-weight: 600;
								}
							}
						}
					}
				}
				&__buy
				{
					text-align: center;
					a
					{
						color: #fff;
						background-color: $red;
						padding: 10px 15px;
						display: flex;
						justify-content: center;
						font-weight: 600;
						font-size: 16px;
						line-height: 30px;
					}
				}
			}
		}
	}
	.file-uploader
	{
		.qq
		{
			&-upload-status-text
			{
				display: block;
				text-align: center;
			}
			&-uploader
			{
				padding: 15px;
				background: #f6f6f6;
				border:1px solid #ddd;
				min-height: 154px;
				position: relative;
				border-radius: 5px;
				&:before
				{
					content: attr(qq-drop-area-text) " ";
					position: absolute;
					font-size: 15px;
					left: 0;
					right: 0;
					text-align: center;
					bottom: 15px;
					opacity: .25;
				}
				button
				{
					height: 20px;
					line-height: 20px;
					background: #333;
					color: #fff;
					border: none;
					border-radius: 10px;
					text-align: center;
					font-size: 11px;
					margin:0 3px;
					padding: 0 10px;
					i
					{
						display: block;
						line-height: inherit;
					}
				}
			}
			&-hide
			{
				display: none
			}
			&-upload-list
			{
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				justify-content: center;
				li
				{
					padding: 0 10px;
					flex: 0 0 200px;
					max-width: 200px;
					margin-bottom: 15px;
					.qq_col
					{
						border-radius: 5px;
						background: #fff;
						box-shadow: 0 0 10px rgba(0,0,0,0.1);
						padding: 10px;
					}
					&.qq-upload-success
					{
						.qq_col
						{
							background: #e8f5e9;
							border-color: #4caf50
						}
					}
					.qq-upload-fail
					{
						.qq_col
						{
							background: #ffebee;
							border-color: #ef5350
						}
					}
				}
			}
			&-file-info
			{
				display: flex;
				align-items: center;
				justify-content: space-between;
				flex-wrap: wrap;
			}
			&-file-name
			{
				.qq-upload-file
				{
					text-overflow: ellipsis;
					overflow: hidden;
					white-space: nowrap;
					display: block;
				}
			}
			&-btns
			{
				display: flex;
				flex: 0 0 100%;
				justify-content: center;
				padding: 10px 0 0;
			}
			&-upload-button
			{
				text-align: center;
				margin-bottom: 15px;
				.btn
				{
					background: #1e88e5;
					color: #fff;
					line-height: 35px;
					padding: 0 10px;
					i
					{
						float: right;
						margin-left: 10px;
						font-size: 11px;
						line-height: inherit;
					}
				}
			}
			&-thumbnail-wrapper
			{
				margin: 5px 0;
				img
				{
					width: 100%;
					display: block;
					height: 180px;
					object-fit: cover;
				}
			}
			&-upload-drop-area
			{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				min-height: 30px;
				z-index: 2;
				background: #F9F9F9;
				border-radius: 4px;
				text-align: center;
			}
			&-drop-processing:not(.qq-hide)
			{
				text-align: center;
				font-size: 13px;
				display: block;
			}
		}
	}
}

