@media (max-width: 1200px)
{
	.profile
	{
		&__content
		{
			&__wallets
			{
				&__item
				{
					flex-wrap: wrap;
					&__actions
					{
						flex: 0 0 calc(100% + 5px);
						max-width: calc(100% + 5px);
						margin: 10px -2px -5px;
						a
						{
							flex: 0 0 calc(25% - 5px);
							max-width: calc(25% - 5px);
						}
					}
				}
			}
		}
	}
}
@media (max-width: 992px)
{
	.profile
	{
		&__content
		{
			&__wallets
			{
				&__item
				{
					flex-wrap: wrap;
					&__price
					{
						flex: 0 0 calc(100% + 8px);
						max-width: calc(100% + 8px);
						margin-top: 10px;
						margin: 10px -4px 0;
						&__item
						{
							flex: 0 0 calc(25% - 8px);
							max-width: calc(25% - 8px);
							margin: 0 4px;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 768px)
{
	.profile
	{
		padding: 20px 0;
		$self: &;
		&__wrapper
		{
			&--collapse
			{
				#{$self}__sidebar
				{
					right: 0;
					nav
					{
						box-shadow: -10px 0 10px -5px rgba(0, 0, 0, 0.05)
					}
				}
				#{$self}__content
				{
					filter: blur(5px);
				}
			}
		}
		&__header
		{
			&__toggle
			{
				display: flex;
			}
		}
		&__sidebar
		{
			@include absolute(0,-220px,0,f);
			z-index: 999;
			background-color: #fff;
			width: 220px;
			nav
			{
				box-shadow: none
			}
		}
		&__content
		{
			flex: 100%;
			max-width: 100%;
			padding: 10px;
			&__form
			{
				&__wrapper
				{
					padding: 10px;
					.form-group
					{
						padding: 10px;
						&-btn
						{
							padding: 0;
						}
						&-modal
						{
							padding: 0;
						}
					}
				}
				&__timer
				{
					margin: -10px;
					padding: 10px;
					img
					{
						width: 40px;
						flex: 0 0 40px;
						max-width: 40px;
					}
					span
					{
						flex: 0 0 calc(100% - 40px);
						max-width: calc(100% - 40px);
					}
				}
				.select-network
				{
					&__item
					{
						label
						{
							padding: 0 15px;
							line-height: 25px;
							font-size: 14px;
						}
					}
				}
			}
			&__wallets
			{
				&__list
				{
					padding: 10px;
				}
			}
		}
	}
}

@media (max-width: 576px)
{
	.profile
	{
		&__content
		{
			&__wallets
			{
				&__item
				{
					padding: 10px;
					&__image
					{
						i
						{
							font-size: 25px;
							line-height: 25px;
						}
						img
						{
							height: 25px;
						}
					}
					&__title
					{
						flex-direction: row;
						justify-content: space-between;
						align-items: center;
						flex: 0 0 calc(100% - 50px);
						max-width: calc(100% - 50px);
						line-height: 25px;
						font-size: 14px;
						small
						{
							font-size: 12px;
						}
					}
					&__actions
					{
						a
						{
							flex: 0 0 calc(50% - 5px);
							max-width: calc(50% - 5px);
						}
					}
					&__price
					{
						flex-wrap: wrap;
						margin-bottom: -8px;
						&__item
						{
							flex: 0 0 calc(50% - 8px);
    						max-width: calc(50% - 8px);
    						margin-bottom: 8px;
							span
							{
								flex-direction: row;
								small
								{
									margin-top: 0;
									margin-right: 5px;
								}
							}
						}
					}
				}
			}
		}
	}
}