.factor
{
    margin-bottom: 50px;
    &__list
    {
        box-shadow: 0 0 25px rgba(0,0,0,0.15);
        padding: 25px 35px;
        border-radius: 15px;
        margin-bottom: 30px;
        ul
        {
            li
            {
                display: flex;
                align-items: center;
                justify-content: space-between;
                line-height: 50px;
                border-bottom: 1px dashed #eee;
                &:last-child
                {
                    border-bottom: none;
                }
                span
                {
                    font-size: 13px;
                    &:last-child
                    {
                        font-weight: 600;
                        font-size: 14px;
                    }
                    small
                    {
                        margin-right: 5px;
                    }
                }
            }
        }
    }
    &__alert
    {
        padding: 20px;
        border-radius: 15px;
        text-align: center;
        border: 1px solid #ccc;
        margin-bottom: 30px;
        ul
        {
            li
            {
                line-height: 40px;
                font-weight: 600;
                color: #666;
                font-size: 13px;
            }
        }
    }
    &__btn
    {
        .btn
        {
            line-height: 58px;
            height: 60px;
            width: 100%;
            border-radius: 10px;
            background-color: $color;
            color: #fff;
            font-size: 16px;
        }
    }
}

@media(max-width:992px)
{
    .factor
    {
        &__list
        {
           padding: 25px 20px;
        }
    }
}