.about
{
	padding: 80px 0;
	margin-bottom: 50px;
	position: relative;
	background-color: rgba(228, 202, 184, 0.1);
	&__slider
	{
		&__item
		{
			height: 500px;
			border-radius: 15px;
			overflow: hidden;
			img
			{
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	&__content
	{
		height: 100%;
		display: flex;
		flex-direction: column;
	}
	&__desc
	{
		p
		{
			color: #454545;
			line-height: 40px;
			font-size: 15px;
			font-weight: 300;
			text-align: justify;
			margin-bottom: 0;
		}
		h2
		{
			line-height: 40px;
			font-weight: 300;
			font-size: 20px;
			color: #222;
			margin-bottom: 25px;
		}
	}
	
	&__coustomers
	{
		margin-top: auto;
		background-color: #f5f5f5;
		border-radius: 15px;
		padding: 15px;
		display: flex;
		align-items: center;
		justify-content: space-around;
		// flex-wrap: wrap;
		&__item
		{
			margin-bottom: 15px;
			
			transition: 0.3s;
			img
			{
			 max-width: 90%;
			}
			&:hover
			{
				padding: 0 15px;
			}
		}
	}
}
@media(max-width:768px)
{
	.about
	{
		margin-bottom: 20px;

		&__slider
		{
			&__item
			{
				height: 300px;
			}
		}
	
	padding: 0;
	&__desc
	{
		margin-top: 20px;
		margin-bottom: 10px;
		
		h2
		{
			margin-bottom:5px;;
		}
	}

	}
}

@media(max-width:567px)
{
	.about
	{
		
		margin-bottom: 20px;
		&__slider
		{
			&__item
			{
				height:250px;
				margin-left: 15px;
				flex: 0 0 20px;
			}
		}
		&__coustomers
		{
		

			&__item
			{
				margin-left: 15px;
				&:hover
			{
				padding: 0 ;
			}
			}
		
		}
		&__desc
	{
		
		h2
		{
			margin-bottom:5px;;
		}
	}

	}
}
