.intro
{
    overflow: hidden;
    position: relative;
    margin-bottom: 70px;
    $slider: &;
    .container
    {
        position: relative;
    }
    &__right
    {
        @include absolute(20px,35px,20px,f);
        width: 280px;
        background-color: #fff;
        z-index: 999;
        box-shadow: 0 0 25px rgba(0,0,0,0.15);
        border-radius: 15px;
        padding-top: 5px;
        padding-right: 20px;
        padding-left: 20px;
        display: flex;
        flex-direction: column;
    }
    &__logo
    {
        border-bottom: 1px solid #eee;
        // padding-bottom: 20px;
        // margin-bottom: 20px;
        img
        {
            max-width: 200px;
            max-width: 200px;
            margin: 0 auto;
            display: block;
        }
    }
    &__login
    {
        display: flex;
        justify-content: center;
        text-align: center;
        a
        {
            line-height: 35px;
            display: flex;
            align-items: center;
            font-weight: 600;
            font-size: 13px;
            transition: 0.3s;
            i
            {
                margin-left: 5px;
            }
            &:hover
            {
                color: $color;
            }
        }
    }
    &__nav
    {

        nav
        {
            >ul
            {
                >li
                {
                    line-height: 35px;
                    border-bottom: 1px solid #f5f5f5;
                    &:last-child
                    {
                        border-bottom:none;
                    }
                    >a
                    {
                        display: block;
                        line-height: inherit;
                        font-weight: 600;
                        font-size: 13px;
                        transition: 0.3s;
                    }
                    &:hover
                    {
                        >a
                        {
                            color: $color;
                        }
                    }
                }
            }
        }
    }
    
    &__item
    {
        height: 550px;
        border-radius: 15px;
        position: relative;
        overflow: hidden;
        &__image
        {
            height: 100%;
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__overlay
        {
            @include absolute(f,325px,-150px,20px);
            background-color: rgba(234,172,28,.85);
            border-radius: 15px;
            display: flex;
            justify-content: space-between;
            padding: 25px;
            align-items: center;
            color: #fff;
            transition-duration: 1s;
            span
            {
                display: flex;
                align-items: center;
                font-size: 15px;
                font-weight: 300;
                text-align: center;
                b
                {
                    font-size: 18px;
                    margin-left: 10px;
                }
            }
        }
    }
    .swiper
    {
        &-container
        {
            position: relative;
            overflow: visible;
        }
        &-slide
        {
            opacity: 0.15;
            transition: 0.75s;
            &-active
            {
                opacity: 1;
                #{$slider}__item__overlay
                {
                    bottom: 20px;
                    transition-delay: 0.5s;
                }
            }
        }
    }
}

@media(max-width:992px)
{
    .intro
    {
        &__right
        {
            width: 230px;

        }
        &__item
        {
            
            &__overlay
            {
                right: 270px;
       
               
            
            
            } 
        }
    }
}


@media(max-width:768px)
{
    .intro    
    {
        margin-bottom: 20px;
        &__right
        {
            display:none;
        }
        &__item
        {
            height: 420px;
            &__overlay
            {
                right: 20px;
                span
                {
                    display: flex;
                    align-items: center;
                    font-size: 15px;
                    font-weight: 300;
                    text-align: center;
                    b
                    {
                        font-size: 18px;
                        margin-left: 10px;
                    }
                }
            
            
            }  
        }
    }
}

@media(max-width:576px)
{
    .intro
    {
        margin-bottom: 20px;
        &__item
        {
            height: 320px;
        }
    }
}

@media(max-width:480px)
{
    .intro    
    {
        $slider: &;
        margin-bottom: 20px;
        &__right
        {
            display:none;
        }
        &__item
        {
            height: 200px;
            justify-content: center;
            
            &__overlay
            {
                right: 10px;
                left: 10px;
                bottom: 10px;
                padding: 10px; 
                span
                {
                    font-size: 12px;
                    b
                    {
                        font-size: 13px;
                        margin-left: 5px;
                    }
                }
                a
                {
                    padding: 0 10px;
                    font-size: 12px;
                    line-height: 30px;
                }
            
            
            }  
        }
        .swiper
        {
            &-slide
            {
                &-active
                {
                    #{$slider}__item__overlay
                    {
                        bottom: 10px;
                    }
                }
            }
        }
    }
}