body
{
	text-align: right;
	direction: rtl;
	font-family: yekan;
	font-size: 12px;
	font-weight: 400;
	font-style: normal;
	color: #212121;
	background-color: $bg;
	padding-top: 30px;
	&.has-header
	{
		padding-top: 90px;
	}
	&.no-header
	{
		.main-header
		{
			display: none;
		}
	}
}


@media (max-width: 768px) {
	body
	{
		&.has-header
		{
			padding-top: 50px;
		}
		padding-top: 60px;
		&.no-header
	{
		.main-header
		{
			display:block;
		}
	}

	}
}
