.services
{
	margin-bottom: 40px;
	&__item
	{
		border-radius: 0 0 15px 0;
		padding: 0 20px 30px 0;
		transition: 0.2s;
		position: relative;
		margin-bottom: 30px;
		box-shadow: 15px 15px 30px -15px rgba(0,0,0,0.1);
		$self: &;
		&__title
		{
			margin-bottom: 10px;
			padding-bottom: 10px;
			border-bottom: 1px solid #eee;
			a
			{
				font-size: 13px;
				font-weight: 600;
				color: $color2;
				line-height: 30px;
				transition: 0.2s;
				z-index: 15;
			}
		}
		&__image
		{
			margin-bottom: 10px;
			height: 150px;
			background-color: $color;
			border-radius: 10px 10px 10px 0;
			img
			{
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 10px 10px 10px 0;
				opacity: 0.75;
				transition: 0.3s;
			}
		}
		&__content
		{
			line-height: 28px;
			text-align: justify;
			color: #666;
		}
		>a
		{
			@include absolute(f,f,10px,0);
			z-index: 2;
			padding: 0 10px;
			background-color: $color;
			color: #fff;
			font-size: 11px;
			font-weight: 600;
			line-height: 24px;
			border-radius: 0 12px 12px 12px;
			transition: 0.3s;
		}
		&:hover
		{
			>a
			{
				background-color: #555;
			}
			#{$self}__image
			{
				img
				{
					opacity: 1;
				}
			}
		}
	}
}

@media(max-width:768px)
{
	.services
	{
		margin-bottom: 0;
	}
}
@media(max-width:576px)
{
	.services
	{
		margin-bottom: 0;
		&__item
		{
			padding: 0 10px 30px 10px;
			box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.1);
			a
			{
				left: 10px;
			}
		}
	}
}