.btn,
a.btn
{
	line-height: 43px;
    display: inline-block;
	padding: 0 15px;
	border: 1px solid $color;
    border-radius: 15px 15px 15px 0;
    transition: 0.3s;
    font-size: 14px;
    font-weight: 600;
    background-color:transparent;
    color: $color;
    position: relative;
    text-align: center;
	box-shadow: none;
    &:focus,
	&.focus
    {
    	background-color: $color;
    	color: #fff;
   		box-shadow: none;
   		outline: none;
    }
    &:hover
    {
    	background-color: $color;
    	color: #fff;
   		box-shadow: none;
   		outline: none;
    }
    &:active
    {
    	outline: none;
    }
    &-success
    {
    	color: #fff;
    	background-color: #00ac5f;
		box-shadow: 0 0 10px rgba(0, 172, 95, 0.4);
    	&:hover,:focus
    	{
    		color: #fff;
    		background-color: #00ac5f;
			box-shadow: 0 0 20px rgba(0, 172, 95, 0.4);
    	}
	}
	&-white
	{
		color: #fff;
		background-color: transparent;
		border-color: #fff;
		&:hover,:focus
    	{
    		color: $color;
			background-color: #fff;
			border-color: #fff;
    	}
	}
    &-mini
    {
    	line-height: 24px;
    	padding: 0 15px;
    	border-radius: 12px;
    }
	&-full
	{
		width: 100%;
	}
	
}

.form-control
{
	border-radius: 8px;
	border: 1px solid #ddd;
	box-shadow: none;
	outline: none;
	font-size: 13px;
	font-weight: 400;
	line-height: 43px;
	height: 45px;
	color: #222;
	background-color: transparent;
	padding: 0 15px;
	display: block;
	width: 100%;
	transition: 0.3s;

	&:focus,&:active
	{
		outline: none;
		color: #222;
		box-shadow: none;
		background-color: transparent;
		border-color: #999;
	}
	&:disabled,&[readonly]
	{
		background-color: transparent;
    	opacity: 1;
	}
	&:last-child
	{
		margin-bottom: 0
	}
	&--dark
	{
		background-color: #1d1f27;
		border-color: #1d1f27;
		&:focus,&:active
		{
			background-color: #1d1f27;
			border-color: #1d1f27;
		}
	}
	&.bootstrap-select
	{
		background-color: transparent;
		&.show
		{
			> .btn-light
			{
				&.dropdown-toggle
				{
					background-color: transparent;
					color: $gray;
					border-color: $gray;
				}
			}
		}
	}
	&[type=email], &[type=file], &[type=number], &[type=password]
	{
		text-align: right;
		direction: rtl;
	}
}

.form-check-input
{
	margin-top: 2px;
}

.input-group-text
{
	background-color: #f1f1f1;
	font-size: 14px;
	border: none;
}

textarea.form-control
{
	min-height: 200px;
	border-width: 1px;
	padding: 0 15px;
	&:focus,&:active
	{
		min-height: 200px;
	}
}

.form-group
{
	h3
	{
		line-height: 30px;
		font-size: 16px;
		border-bottom: 1px solid #eee
	}
	>label
	{
		font-size: 12px;
	}
	&-btn-center
	{
		text-align: center;
	}
	&-btn-left
	{
		text-align: left;
	}
	&-inline
	{
		display: flex;
		justify-content: space-between;
		>label
		{
			width: 90px;
			line-height: 50px;
			margin: 0
		}
		>.form-control,
		>.input-group,
		>.bootstrap-select.form-control:not([class*=col-])
		{
			width: calc(100% - 90px)
		}
	}
	&-last
	{
		margin-bottom: 0
	}
	&-icon
	{
		position: relative;
		input
		{
			padding-right: 30px;
		}
		i
		{
			@include absolute(0,0,0,f);
			color: #6c717f;
			line-height: 40px;
			text-align: center;
			font-size: 18px;
		}
	}
}

.dropdown-menu
{
	padding: 0;
	box-shadow: 0 0 20px rgba(0,0,0,0.2);
	border-radius: 5px;
	background-color: $bg;
	border: none;
	.dropdown-item
	{
		padding: 0 10px;
		line-height: 35px;
		font-size: 13px;
		color: #000;
		&.hover,
		&:hover
		{
			background-color: rgba(0,0,0,0.1);
			color: #333;
		}
		&.active,
		&:active
		{
			background-color: rgba(0,0,0,0.2);
			color: #333;
			outline: none;
		}
	}
}

.bootstrap-select
{
	> .dropdown-toggle
	{
		text-align: right;
		border:‌ none;
		padding: 0 15px;
		font-size: 13px;
		font-weight: 600;
		line-height: 40px;
		color: #333;
		border-radius: 5px;
		background-color: transparent;
		background-image: none;
		box-shadow: none;
		&:focus
		{
			outline: unset !important;
			outline-offset: unset;
			border-color: transparent transparent #6c717f;
		}
		&:after
		{
		    margin-right: unset;
		    vertical-align: unset;
		    content: "\e91d";
		    font-family: 'denta';
		    border-top: unset;
		    border-right: unset;
		    border-bottom: unset;
		    border-left: unset;
		    line-height: inherit;
		    font-size: 8px;
		    margin:0 5px 0 0;
		}
		&.btn-light:not(:disabled):not(.disabled).active,
		&.btn-light:not(:disabled):not(.disabled):active
		{
			background-color: transparent;
			box-shadow: none;
			border-color: transparent transparent #6c717f;
			outline: none;
			color: #333;
		}
		&.bs-placeholder,
		&.bs-placeholder:active,
		&.bs-placeholder:focus,
		&.bs-placeholder:hover
		{
			color:#333;
		}
	}
	.filter-option-inner-inner
	{
		text-align: right;
	}
	> select.mobile-device
	{
		&:focus
		{
			+ .dropdown-toggle
			{
				outline: unset;
				outline-offset: unset
			}
		}
	}
	&.show > .btn-light.dropdown-toggle
	{
		background-color: transparent;
		box-shadow: unset;
	}
}
