.blog
{
	margin-bottom: 40px;
	&__item
	{
		$self: &;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		border-radius: 15px;
		height: 255px;
		padding: 10px;
		transition: 0.2s;
		position: relative;
		overflow: hidden;
		background-color: #fff;
		margin-bottom: 30px;
		box-shadow: 0 5px 15px rgba(0,0,0,0.1);
		&__background
		{
			@include absolute(-10px,-10px,-10px,-10px);
			border-radius: 20px;
			overflow: hidden;
			z-index: 1;
			
			&:before
			{
				@include absolute(0,0,0,0);
				content: '';
				background-color: rgba(0,0,0,0.35);
				z-index: 5;
			}
			img
			{
				width: 100%;
				height: 100%;
				object-fit: cover;
				filter: blur(5px);
			}
		}
		&__title
		{
			position: relative;
			z-index: 2;
			margin-bottom: 0;
			transition: 0.3s;
			h2
			{
				color: #fff;
				font-size: 20px;
				line-height: 40px;
				font-weight: 300;
			}
		}
		&__info
		{
			position: relative;
			z-index: 2;
			display: flex;
			align-items: center;
			justify-content: center;
			transform: scale(0);
			transition: 0.3s;
			height: 0;
			span
			{
				color: #fff;
				margin-left: 20px;
				font-size: 15px;
				font-weight: 300;
				display: flex;
				align-items: center;
				flex-direction: row-reverse;
				line-height: 20px;
				&:last-child
				{
					margin-left: 0;
				}
				i
				{
					margin-right: 5px;
				}
			}
		}
		&:hover
		{
			#{$self}__title
			{
				margin-bottom: 20px;
			}
			#{$self}__info
			{
				transform: scale(1);
				height: 20px;
			}
		}
		a
		{
			@include absolute(0,0,0,0);
			z-index: 2;
		}
	}
}

@media(max-width:768px)
{
	.blog
	{
	margin-bottom:0px;

		// &__item
		// {
		// 	&:hover{
		// 		display: none;
		// 	}
		// }
	}
}
@media(max-width:576px)
{
	.blog
	{
		&__item
		{
			margin-bottom: 20px;
			height: 160px;
			&__title
			{
				margin-bottom: 20px;
			}
			&__info
			{
				transform: scale(1);
				height: 20px;
			}
			&__title
			{
				h2
				{
					font-size: 15px;
				}
			}
		}
	}
}