.comments {
    margin-bottom: 50px;

    &__item {
        border: 1px solid #ddd;
        position: relative;
        margin-bottom: 30px;
        padding: 10px 15px 5px;
        border-radius: 15px;

        &__header {
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            padding: 5px;
            background-color: #fff;
            border-bottom: 1px solid #eee;

            >span {
                line-height: 30px;
                font-size: 13px;
                font-weight: 500
            }

            &__avatar {
                @include border-radius(50%);
                margin-left: 15px;
                width: 30px;
                height: 30px;
                background-color: #f5f5f5;
                overflow: hidden;

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                }
            }

            &__date {
                line-height: 30px;
                font-size: 11px;
                margin-right: auto;
            }
        }

        &__body {
            text-align: justify;
            line-height: 35px;
            font-size: 14px;
            color: #666;
            font-weight: 300;
            margin-bottom: 20px;

            p {
                margin-bottom: 0;
            }
        }

        &__footer {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: row-reverse;
            border-top: 1px solid #eee;

            a {
                line-height: 30px;
                color: #888;
                font-size: 11px;
                display: flex;
                align-items: center;
                flex-direction: row-reverse;

                &:last-child {
                    margin-right: 0
                }

                i {
                    font-size: 15px;
                    line-height: inherit;
                    margin-right: 5px;
                }
            }
        }

        &--reply {
            margin-right: 40px;
        }
    }

    &__form {
        margin-bottom: 50px;
    }

    &__filds {
        border-radius: 10px;
        border: 1px solid #DDD;
        padding: 15px 15px 0;

        textarea.form-control {
            padding: 0;
            border: none;
            background-color: transparent;

            &:focus {
                background-color: transparent;
            }
        }
    }
}

@media(max-width: 480px) {
    .comments {
        &__item {
            &__header {
                flex-direction: column;
                justify-content: center;
                text-align: center;
                align-items: stretch;
                &__date {
                    margin-right: 0;
    
                }
    
                &__avatar {
                    margin-bottom: 10px;
                    margin: 0 auto;
                
                }
            }

           
        }
    }
}
