.page-title
{
    padding: 45px 0;
    position: relative;
    overflow: hidden;
    margin-bottom: 50px;
    &__background
    {
        @include absolute(-10px,-10px,-10px,-10px);
        z-index: 1;
        background-image: url('../img/salon.jpg');
        background-attachment: fixed;
        background-position: center;
        background-size: cover;
        filter: blur(5px);
        overflow: hidden;
        &:before
        {
            content: '';
            @include absolute(0,0,0,0);
            background-color: rgba(0,0,0,0.3);
        }
    }
    .container
    {
        position: relative;
        z-index: 2;
    }
    &__wrapper
    {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #fff;
        h1
        {
            font-size: 24px;
            font-weight: 600;
        }
        ul
        {
            display: flex;
            align-items: center;
            li
            {
                margin-left: 15px;
                a
                {
                    color: #fff;
                }
                &:after
                {
                    content: '/';
                    margin-right: 15px;

                }
                &:last-child
                {
                    margin-left: 0;
                    padding-left: 0;
                    &:after
                    {
                        display: none;
                    }
                }
            }
        }
    }
}

@media(max-width:992px)
{
    .page-title
    {
        &__wrapper
        {
            
           h1
           {
               font-size: 20px;
           }
            
            
        }
    }
}
@media(max-width:768px)
{
    .page-title
    {
        &__wrapper
        {
            flex-direction: column;
            justify-content: center;
            h1{
                font-size: 24px;
                margin-bottom: 15px;
            }
        }
    }
}
@media(max-width:480px)
{
    .page-title
    {
        &__wrapper
        {
            h1{
                font-size:15px;
            }
            ul
            {
                display: flex;
                align-items: center;
                li
                {
                    margin-left: 6px;
                    font-size: 11px;
                    a
                    {
                        color: #fff;
                    }
                    &:after
                    {
                        content: '/';
                        margin-right: 6px;
    
                    }
                    &:last-child
                    {
                        margin-left: 0;
                        padding-left: 0;
                        &:after
                        {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}

@media(max-width:576px)
{
    .page-title
    {
        margin-bottom: 20px;
    }
}