.main-header
{
	padding: 10px 0;
	@include fixed(0,0,f,0);
	backdrop-filter: blur(5px);
	z-index: 99;
	transition: 0.3s;
	background-color: #fff;
	overflow: hidden;

	box-shadow: 0 0 25px rgba(0,0,0,0.1);


	&__mobie-menu
	{
		display: none;
		margin-left: 15px;
		font-size: 18px;
		a
		{
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
	&__wrapper
	{
		display: flex;
		align-items: center;
	}
	&__logo
	{
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		margin-left: 30px;
		img
		{
			max-height: 70px;
		}
		a
		{
			font-size: 18px;
			font-weight: 600;
			line-height: 20px;
		}
		span
		{
			font-weight: 300;
			color: #666;
			line-height: 20px;
		}
	}
	&__nav
	{
		ul
		{
			display: flex;
			align-items: center;
			li
			{
				margin-left: 30px;
				a
				{
					font-weight: 600;
					display: flex;
					align-items: center;
					color: #222;
					line-height: 20px;
					transition: 0.3s;
					font-size: 13px;
					i
					{
						margin-left: 10px;
						font-size: 15px;
					}
				}
				&:hover
				{
					>a
					{
						color: $color;
					}
				}
				&:last-child
				{
					margin-left: 0;
				}
			}
		}
	}
	&__left
	{
		margin-right: auto;
		ul
		{
			display: flex;
			flex-direction: row-reverse;
			align-items: center;
			
			li
			{
				a
				{
					font-weight: 600;
					display: flex;
					align-items: center;
					flex-direction: row-reverse;
					color: #222;
					line-height: 20px;
					i
					{
						margin-right: 10px;
						font-size: 15px;
					}
				}
				&:hover
				{
					>a
					{
						color: $color;
					}
				}
			}
		}
	}
}


@media (max-width: 768px)
{
	.main-header
	{
		padding: 5px 0;
		&__wrapper
		{
			justify-content: flex-start;
		}
		&__mobie-menu
		{
			display: flex;
			align-items: center;
			justify-content: center;
			order: -2;
		}
		&__right
		{
			margin-right: auto;
		}
		&__left
		{
			ul
			{
				li
				{
					a
					{
						flex-direction: row;
						i
						{
							margin: 0 0 0 5px;
						}
					}
				}
			}
		}
		&__logo
		{
			img
			{
				max-height: 40px;
			}
			order: -1;
			text-align: right;
			a
			{
				margin-bottom: 0;
				font-size: 14px;
			}
			span
			{
				display: none;
			}
		}
		&__nav
		{
		display: none;
		}
	}
}
