.auth
{
    position: relative;
    &__background
    {
        @include absolute(0,0,0,0);
        z-index: 1;
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
            opacity: 0.4;
        }
        &:before
        {
            content: '';
            @include absolute(0,0,0,0);
            background-image: linear-gradient(to bottom,rgba(255,255,255,1),rgba(255,255,255,0));
            z-index: 2;
        }
    }
    .container
    {
        position: relative;
        z-index: 2;
    }
    &__wrapper
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        overflow: hidden;
        margin-top: -30px;
        min-height: 100vh;
        padding: 30px 0;
        &:before
        {

        }
    }
    &__logo
    {
        margin-bottom: auto;
        display: flex;
        flex-direction: column;
        text-align: center;
        img
        {
            max-width: 250px;
            margin-bottom: 20px;
        }
        span
        {
            font-weight: 600;
            color: #212121;
            font-size: 13px;
        }
    }
    &__form
    {
        width: 400px;
        max-width: 100%;
        padding: 15px;
        background-color: rgba(0,0,0,0.4);
        border-radius: 12px;
        margin: 15px 0;
        .form-control
        {
            background-color: #eee;
            border: none;
            line-height: 60px;
            height: 60px;
            border-radius: 10px;
            font-weight: 600;
            font-size: 14px;
            padding: 0 20px;
        }
        .btn
        {
            line-height: 58px;
            height: 60px;
            width: 100%;
            border-radius: 10px;
            background-color: $color;
            color: #fff;
            font-size: 18px;
            margin-top: 20px;
        }
        form
        {
            margin-bottom: 25px;
        }
        ul
        {
            li
            {
                text-align: center;
                font-size: 13px;
                font-weight: 600;
                color: #fff;
                a
                {
                    color: $color;
                }
            }
        }
    }
    &__nav
    {
        margin-top: auto;
        ul
        {
            display: flex;
            align-items: center;
            li
            {
                margin-left: 40px;
                &:last-child
                {
                    margin-left: 0;
                }
                a
                {
                    font-weight: 600;
                    color: #212121;
                    font-size: 13px;
                }
            }
        }
    }
}


@media (max-width: 992px) {
    .auth
    {
        &__wrapper
        {
        }
    }
}
@media (max-width: 768px)
{
    .auth
    {
        &__wrapper
        {
            margin-top: -60px;
        }
        &__logo
        {
        }
        &__form
        {
        }
        &__nav
        {
            ul
            {
                li
                {
                    margin-left: 15px;
                    &:last-child{
                        margin-left: 0;
                    }
                }
            }
        }
    }
}

@media (max-width: 576px)
{
    .auth
    {
        &__form
        {
            .form-control
            {
                line-height: 45px;
                height: 45px;
                border-radius: 5px;
            }
            .btn
            {
                line-height: 43px;
                height: 45px;
                border-radius: 5px;
                font-size: 16px;
            }
        }
    }
}
@media(max-width:480px)
{
    .auth
    {
        &__nav
        {
            ul
            {
                li
                {
                    margin-left: 12px;
                    a
                    {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}