ul
{
	padding: 0;
	margin: 0;
	li
	{
		list-style: none
	}
}

a,
a:hover,
a:focus,
a:active,
a:visited
{
	text-decoration: none;
	color: #212121
}

img
{
	max-width: 100%
}

h1,h2,h3,h4,h5,h6
{
	margin: 0
}

input,button,textarea
{
	&:focus
	{
		outline: none;
	}
}

@media (min-width: 1200px)
{
	.container
	{
		max-width: 1200px;
	}
}