.page-header
{
	// background-color: rgba(228, 202, 184, 0.2);
	text-align: center;
	padding: 30px 0;
	position: relative;
	&:before
	{
		content: '';
		@include absolute(70px,0,f,0);
		border-top: 2px dashed rgba(228, 202, 184, 0.2);
	}
	h1,h2,h3,h4,h5,h6
	{
		line-height: 30px;
		font-size: 25px;
		font-weight: 300;
		color: $color2;
	}
}