.cat-blog {
    &__item {
        $self: &;
        border-radius: 10px;
        height: 300px;
        position: relative;
        margin-bottom: 40px;

        &:before {
            content: '';
            @include absolute(f, 15px, -8px, 15px);
            border-radius: 0 0 8px 8px;
            background-color: $color;
            height: 8px;
        }

        >a {
            @include absolute(0, 0, 0, 0);
            border-radius: 10px;
            z-index: 10
        }

        &__image {
            height: 100%;
            border-radius: 10px;
            overflow: hidden;
            position: relative;
            z-index: 1;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: block;
                transition: 0.3s;
            }
        }

        &__content {
            @include absolute(0, 0, 0, 0);
            border-radius: 10px;
            padding: 15px;
            z-index: 2;
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
            color: #fff;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            &__title {
                margin-bottom: 15px;

                h2 {
                    font-size: 13px;
                    font-weight: 600;
                    line-height: 22px;

                    a {
                        color: #fff;
                    }
                }
            }

            &__info {
                display: flex;
                align-items: center;

                span {
                    margin-left: 20px;
                    display: flex;
                    align-items: center;
                    font-size: 11px;

                    &:last-child {
                        margin-left: 0;
                    }

                    i {
                        margin-left: 5px;
                    }

                    &:last-child {
                        margin-right: auto;
                    }
                }
            }

            &__desc {
                margin-bottom: 0;
                font-weight: 300;
                text-align: justify;
                position: relative;
                overflow: hidden;
                transition: 0.3s;
                line-height: 20px;
                height: 0;

                p {
                    color: rgba(255, 255, 255, 0.95);
                    margin-bottom: 0;
                }
            }
        }

        &:hover {
            #{$self}__content__desc {
                height: 40px;
                margin-bottom: 20px;
            }

            #{$self}__image {
                img {
                    transform: scale(1.05);
                    filter: blur(5px);
                }
            }
        }
    }

    &__instagram {
        border-radius: 10px;
        margin-bottom: 40px;
        height: 170px;
        background-image: linear-gradient(20deg, #dc1b59, #dc2020);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        color: #fff;
        position: relative;
        padding:30px 20px;

        span {
            font-size: 15px;
            line-height: 30px;
        }

        i {
            font-size: 40px;
            margin-bottom: 20px;
            transition: 0.3s
        }

        a {
            @include absolute(0, 0, 0, 0);
            border-radius: 10px;
        }
    }

    &__telegram {
        border-radius: 10px;
        margin-bottom: 40px;
        height: 170px;
        background-image: linear-gradient(20deg, #627cff, #00b7fd);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        color: #fff;
        position: relative;
        padding:30px 20px;


        span {
            font-size: 15px;
            line-height: 30px;
        }

        i {
            font-size:40px;
            margin-bottom: 20px;
            transition: 0.3s
        }

        a {
            @include absolute(0, 0, 0, 0);
            border-radius: 10px;
        }
    }
    &__title
    {
        
        margin-bottom: 30px;
        position: relative;
        display: flex;

        &:before{
            content: '';
            border-top: 1px solid #cbdae0;
            position: absolute;
            left: 0;
            right: 0;
            top: 50%;
            z-index: 1;


        }
        
        h3
        {
           
            line-height: 40px;
            font-weight: 600;
            font-size: 16px;
             color: #222;
             background-color: white;
             z-index: 2;
             position: relative;
             padding-left: 10px;
           
        }
    }
    &__list
    {
        display: flex;
        border-radius: 15px;
        height: 210px;
        transition: 0.2s;
        position: relative;
        overflow: hidden;
        background-color: #fff;
        margin-bottom: 30px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    
    
            &__img {
                height: 100%;
              border-bottom-right-radius: 10px;
              border-top-right-radius: 10px;
                overflow: hidden;
                position: relative;
                z-index: 1;
    
                img {
                    flex: 0 0 30%; 
                    height: 100%;
                    object-fit: cover;
                    display: block;
                    transition: 0.3s;
                }
            }
            &__desc
            {
                display: flex;
                flex: 0 0 70%;
                padding: 15px;
                height: 100%;
                padding-right: 20px;
                flex-direction: column;
                &__title
                {
                    h2
                    {
                        line-height: 40px;
                        font-weight: 600;
                         font-size: 15px;
                          color: #222;
                         margin-bottom: 10px;
                    }

                }
                &__text
                {
                   p
                   {
                    color:#868686;
                    line-height:30px;
                    height: 90px;
                    overflow: hidden;
                    font-size: 12px;
                    font-weight: 400;
                    text-align: justify;
                    margin-bottom: 0;
                   }
                }
                &__info {
                    display: flex;
                    align-items: center;
                    margin-top: auto;
    
                    span {
                        margin-left: 20px;
                        display: flex;
                        align-items: center;
                        font-size: 11px;
    
                        &:last-child {
                            margin-left: 0;
                        }
    
                        i {
                            margin-left: 5px;
                        }
    
                        &:last-child {
                            margin-right:auto;
                        }
                    }
                }
            }
            >a {
                @include absolute(0, 0, 0, 0);
                border-radius: 10px;
                z-index: 10
            }
    
            &__image {
                height: 100%;
                border-radius: 10px;
                overflow: hidden;
                position: relative;
                z-index: 1;
    
                >img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    display: block;
                    transition: 0.3s;
                }
            }
    
            &__content {
                @include absolute(0, 0, 0, 0);
                border-radius: 10px;
                padding: 15px;
                z-index: 2;
                background-image: linear-gradient(to top, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
                color: #fff;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
    
                &__title {
                    margin-bottom: 15px;
    
                    h2 {
                        font-size: 13px;
                        font-weight: 600;
                        line-height: 22px;
    
                        a {
                            color: #fff;
                        }
                    }
                }
    
                &__info {
                    display: flex;
                    align-items: center;
    
                    span {
                        margin-left: 20px;
                        display: flex;
                        align-items: center;
                        font-size: 11px;
    
                        &:last-child {
                            margin-left: 0;
                        }
    
                        i {
                            margin-left: 5px;
                        }
    
                        &:last-child {
                            margin-right: auto;
                        }
                    }
                }
    
       } 
    }
    
}



@media(max-width: 576px) {
    .cat-blog {
        &__telegram
        {
            margin-bottom: 20px;

        }
        &__instagram
        {
            margin-bottom: 20px;

        }
        &__title
        {
            margin-bottom: 15px;
        }
        &__item {
            margin-bottom: 30px;
            &__content {
                &__desc {
                    display: none;
                }
            }
        }
        &__list
        {
            margin-bottom: 15px;

            flex-direction: column;
            overflow: none;
            height: unset;
            &__img
            {
                height: 150px;
                border-bottom-right-radius: 0;
            a
            {
                img
                {
                  width: 100%;
                }
            }
        }  
            &__desc
            {
                &__title
                {
                    h2
                    {
                        margin-bottom: 0;
                        font-size: 14px;
                    }
                }
                &__text
                {
                    p

                    {
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}
@media(max-width:480px)
{
    .cat-blog
    {
        &__item
        {
            height: 230px;
        }
        &__list
        {
            &__image
            {
               height: 230px;
            }
        }
    }
}