.main-footer
{
	padding: 80px 0 0;
	position: relative;
	overflow: hidden;
	background-color: #f5f5f5;

	&__item
	{
		&__title
		{
			margin-bottom: 30px;
			position: relative;
			display: flex;
			&:before
			{
				content: '';
				height: 1px;
				@include absolute(50%,0,f,0);
				z-index: 1;
				background-color: #ddd;
			}
			h2,h3,h4,h5,h6
			{
				font-size: 14px;
				font-weight: 600;
				z-index: 2;
				position: relative;
				background-color:#f5f5f5;
				padding-left: 15px;
			}
		}
		&__content
		{
			line-height: 35px;
			font-size: 13px;
			text-align: justify;
			ul
			{
				column-count: 2;
				li
				{
					a
					{
						color: #222;
						transition: 0.3s;
						padding-right: 12px;
						position: relative;
						font-size: 13px;
						&:before
						{
							content: '';
							width: 7px;
							height: 1px;
							@include absolute(50%,0,f,f);
							background-color: #555;
						}
						&:hover
						{
							color: $color;

						}
					}
				}
			}
			&--center
			{
				text-align: center;
			}
			p
			{
				margin-bottom: 0;
			}
		}
		&__awards
		{
			border-radius: 15px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			&__item
			{
				transition: 0.3s;
				padding: 10px;
				border-radius: 10px;
				background-color: #fff;
				height: 170px;
				width: 110px;
				display: flex;
				justify-content: center;
				align-items: center;
				img
				{
					max-width: 100%;
					max-height: 150px;
				}
				&:hover
				{
					width: 120px;
				}
			}
		}
	}
	&__copyright
	{
		display: flex;
		margin-top: 50px;
		padding: 10px 0;
		justify-content: space-between;
		align-items: center;
		border-top: 1px solid #ddd;
		p
		{
			margin-bottom: 0;
			line-height: 20px;
			font-size: 11px;
			a
			{
				border-bottom: 1px dashed #666;
			}
		}
	}
}
@media(max-width:1200px)
{
.main-footer
{
	&__item
	{
		&__awards
		{
			border-radius: 15px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			&__item
			{
			
				height: 150px;
				width: 80px;
				
			}
		}
	}
}
}

@media (max-width: 992px)
{
	.main-footer
	{
		&__item
		{
			&__awards
		{
			border-radius: 15px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			&__item
			{
			
				height: 170px;
				width:110px;
				
			}
		}
			margin-bottom: 30px;
			&__title
			{
				
				justify-content: center;
				h2,h3,h4,h5,h6
				{
					padding-right: 15px;
				}

			}
		}
	}
}

@media(max-width:768px)
{
	.main-footer
	{
	padding:30px 0 0;
	&__item
	{
		margin-bottom: 40px;
	}

	}
}

@media(max-width:576px)
{
	.main-footer
	{
		
			&__copyright
			{
				margin-top: 0;
				display: block;
				p
				{
				margin-bottom: 10px;

				}
			}
		
	}
}
@media(max-width:480px)
{
	.main-footer
	{
		&__item
		{
			&__awards
		  { 
		
			&__item
			{
			
				height: 130px;
				width:80px;
				
			}
		}
	}
	}
}