.salon-content
{
    z-index: 50;
    margin-bottom: 80px;
    position: relative;
    &__header
    {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        padding: 20px 0;
        border-bottom: 1px solid #eee;
        &__logo
        {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100px;
            width: 100px;
            flex: 0 0 100px;
            max-width: 100px;
            margin-left: 20px;
            img
            {
                max-width: 100%;
            }
        }
        &__name
        {
            h1
            {
                font-size: 18px;
                font-weight: 600;
                color: #333;
                margin-bottom: 10px;
            }
            p
            {
                margin-bottom: 0;
                color: #777;
            }
        }
        &__social
        {
            margin-right: auto;
            ul
            {
                display: flex;
                align-items: center;
                li
                {
                    margin-left: 15px;
                    &:last-child
                    {
                        margin-left: 0;
                    }
                    a
                    {
                        font-weight: 600;
                        font-size: 20px;
                        color: #777;
                        &:hover
                        {
                            color: $color;
                        }
                    }
                }
            }
        }
    }
    &__content
    {
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    &__map
    {
        margin-top: auto;
        height: 195px;
        box-shadow: 0 3px 35px 0 rgba(0, 0, 0, 0.1);
        border-radius: 15px;
    }
    &__desc
    {
        font-size: 13px;
        line-height: 35px;
        text-align: justify;
        margin-top: 30px;
        padding-bottom: 15px;
        margin-bottom: 25px;
        p
        {
            margin-bottom: 0;
        }
    }
    &__video
    {
        box-shadow: 0 3px 35px 0 rgba(0, 0, 0, 0.1);
        height: 350px;
        video
        {
            width: 100%;
            height: 100%;
        }
        
    }
    &__text
    {
        p
        {
            font-size: 13px;
            line-height: 35px;
            text-align: justify;
            

        }
    }
    &__list
    {
        line-height: 35px;
        font-size: 13px;
        font-weight: 600;
        margin-bottom: 15px;
        ul
        {
            column-count: 2;
            li
            {
                padding-right: 15px;
                position: relative;
                &:before
                {
                    content: '';
                    width: 8px;
                    height: 8px;
                    @include absolute(50%,0,f,f);
                    
                    transform: translateY(-50%) rotate(15deg);
                    background-color: $color;
                }
            }
        }
    }
    &__slider
    {
        box-shadow: 0 3px 35px 0 rgba(0, 0, 0, 0.1);
        border-radius: 15px;
        &__item
        {
            height: 350px;
            position: relative;
            overflow: hidden;
            border-radius: 15px;
            
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    &__tabs
    {
        margin-top: 50px;
        >ul
        {
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            border-bottom: 2px solid #eee;
            margin-bottom: 50px;
            li
            {
                margin-left: 45px;
                font-size: 14px;
                cursor: pointer;
                margin-bottom: -2px;
                line-height: 40px;
                transition: 0.3s;
                border-bottom: 2px solid transparent;
                &.active
                {
                    border-color: $color;
                    font-weight: 600;
                }
                &:last-child
                {
                    margin-left: 0;
                }
            }
        }
        .tab-content
        {
            display: none;
            &.active
            {
                display: block;
            }
        }
    }
    &__ads
    {
        &__item
        {
            box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.1);
            background-color: #fff;
            border-radius: 10px;
            overflow: hidden;
            transition: 0.3s;
            margin-bottom: 30px;
            align-items: center; 
            $item: &;
            &__image
            {
                margin-bottom: 10px;
                height: 250px;
                transition: 0.3s;
                background-color: transparent;
                img
                {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transition: 0.3s;
                }
            }
            &__title
            {
                margin-bottom: 15px;
                padding: 0 15px;
                text-align: center;
                h2
                {
                    font-size: 13px;
                    font-weight: 600;
                    line-height: 30px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
                a
                {
                    transition: 0.3s;
                }
            }
            &__price
            {
                padding: 0 15px 15px;
                font-weight: 600;
                line-height: 30px;
                font-size: 20px;
                color: #40ab75;
                text-align: center;
                margin-right: auto;
                span
                {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                small
                {
                    font-size: 11px;
                    margin-right: 5px;
                    line-height: 10px;
                    color: #878787;
                }
            }
            &:hover
            {
                box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.15);
                #{$item}__title
                {
                    a
                    {
                        color: $color;
                    }
                }
                #{$item}__image
                {
                    background-color: #000;
                    img
                    {
                        opacity: 0.75;
                    }
                }
            }
        }
    }
}

@media(max-width:992px)
{
    .salon-content
    {
        &__desc

        {
            p
            {
                margin-top: 15px;
            }
        }
    }
}

@media(max-width:768px)
{
    .salon-content
    { 
        &__header
        {
            padding: 0 0 20px 0;
            margin-top: 10px;

        }
        // margin-bottom:0px;

        &__slider
        {
            margin-bottom: 10px;
           &__item
           {
            height:300px;

           }
        }
        &__film
        {
            margin-bottom: 10px;
            
        }
        &__desc
        {
            margin-top: 10px;
        }
        &__ads
        {
            &__item
            {
                &__title
                {
                    h2
                    {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}

@media(max-width:576px)
{
   
 
    .salon-content
    {
        &__header
        {
            flex-wrap: wrap;
            justify-content: center;
            &__social
            {
                flex:0 0 100%;
                margin-top: 15px;
                justify-content: center;
                display: flex;
            }
        }
        &__list
        {
            ul
            {
                column-count: 1;
            }
        }
        &__slider
        {
           &__item
           {
            height:250px;

           }
        }
    }
}

@media(max-width:480px)
{
    .salon-content
    {
        &__header
        {
            &__logo
            {
                margin-left: 0;
            }
            &__name
            {
                text-align: center;
                margin-top: 5px;
                flex: 0 0 100%;
            }
        }
        &__film
        {
            margin-bottom: 10px;
            video
            {
                height: 300px;
            }
        }
        &__tabs
        {
            margin-top: 10px;
            ul
            {
                li
                {
                    font-size: 12px;
                    margin-left: 20px;
                }
            }
        }
    }
}