.row-socials
{
	margin-bottom: 40px;
	&__item
	{
		border-radius: 15px;
		padding: 25px;
		display: flex;
		align-items: center;
		justify-content: space-between;
        position: relative;
        margin-bottom: 30px;
		&:before
		{
			content: '';
			@include absolute(15px,10px,5px,10px);
			filter: blur(10px);
			z-index: -1
		}
		&__btn
		{
			a
			{
				display: flex;
				align-items: center;
				border: 1px solid rgba(255,255,255,0.3);
				line-height: 32px;
				border-radius:6px;
				padding: 0 11px;
				font-size: 13px;
                transition: 0.3s;
                color: #fff;
				i
				{
					margin-left: 6px;
					font-size: 8px;
				}
				&:hover
				{
					background-color: #fff;
					border-color: #fff;
					color: #222;
				}
			}
		}
		&__icon
		{
			display: flex;
			align-items: center;
            flex-direction: row-reverse;
            color: #fff;
			i
			{
				margin-right: 11px;
				font-size: 34px;
			}
			span
			{
				font-size: 15px;
			}
		}
		&--instagram
		{
			background-image: linear-gradient(to right,#fa8939,#ff1e7c);
			&:before
			{
				background-image: linear-gradient(to right,#fa8939,#ff1e7c);
			}
		}
		&--telegram
		{
			background-image: linear-gradient(to right,#1a51e3,#1a8ce3);
			&:before
			{
				background-image: linear-gradient(to right,#1a51e3,#1a8ce3);
			}
		}
		&--whatsapp
		{
			background-image: linear-gradient(to right,#128C7E,#075E54);
			&:before
			{
				background-image: linear-gradient(to right,#128C7E#075E54);
			}
		}
	}
}

@media(max-width: 992px)
{
	.row-socials
	{
		margin-bottom: 30px;
		&__item
		{
			height: unset;
			margin-bottom: 20px;
			padding: 25px 10px;
		}
	}
}

@media(max-width: 768px)
{
	.row-socials
	{
		margin-bottom: 10px;
		&__item
		{
			height: unset;
			margin-bottom: 20px;
			padding: 25px 25px;
		}
	}
}

