.mobile-menu
{
	@include fixed(0,-280px,0,f);
	width: 280px;
	background: $bg;
	z-index: 9999999;
	visibility: hidden;
	opacity: 0;
	transition: 0.3s;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
	overflow-y: auto;
	display: flex;
	flex-direction: column;
	&[data-status="true"]
	{
		right: 0;
		visibility: visible;
		opacity: 1;
	}
	nav
	{
		>ul
		{
			>li
			{
				position: relative;
				line-height: 45px;
				>a
				{
					display: block;
					color: black;
					line-height: inherit;
					padding: 0 15px;
					position: relative;
					font-size: 13px;
					i
					{
						float: right;
						line-height: 45px;
						width: 25px;
						margin-right: 5px;
						font-size: 14px;
					}
				}
				>i
				{
					@include absolute(0,f,f,0);
					line-height: inherit;
					width: 45px;
					text-align: center;
					cursor: pointer;
					font-size: 12px;
					color: #333;
					transition: 0.3s;
					z-index: 10;
				}
				&.open
				{
					>a
					{
						background-color: rgba(255, 255, 255, 0.2);
					}
					>i
					{
						transform: rotateX(180deg);
					}
				}
				&:last-child
				{
					a
					{
						border: none;
					}
				}
			
				> ul
				{
					display: none;
					background-color: #f9f9f9;
					li
					{
						>a
						{
							display: block;
							line-height: 45px;
							font-size: 13px;
							border-bottom: 1px solid rgba(0, 0, 0, 0.1);
							padding: 0 15px;
							i
							{
								float: right;
								line-height: 45px;
								width: 25px;
								margin-right: 5px;
								font-size: 15px;
							}
						}
					}
					&:hover
					{
						>a
						{
							background-color: rgba(255, 255, 255, 0.2);
						}
					}
				}
			}
		}
	}
	&__logo
	{
		background-color:#fff;
		padding: 60px 0 20px 0;
		margin-bottom: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		img
		{
			max-width: 70%;
		}
	}
	[data-toggle="closebox"]
	{
		@include absolute(10px,f,f,10px);
	    width: 30px;
	    height: 30px;
	    cursor: pointer;
	    &:before
	    {
	    	@include absolute(15px,f,f,-5px);
			width: 40px;
			height: 1px;
			background-color:#6d6d6d;
			content: '';
			transform: rotate(-45deg);
	    }
	    &:after
	    {
	    	@include absolute(15px,f,f,-5px);
			width: 40px;
			height: 1px;
			background-color:#6d6d6d;

			content: '';
			transform: rotate(45deg);
	    }
	}
}