.banner
{
	position: relative;
	margin-bottom: 70px;
	&__wrapper
	{
		border-radius: 15px;
		overflow: hidden;
		box-shadow:0 25px 45px -25px rgba(0, 0, 0, 0.25);
		img
		{
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
@media(max-width:768px)
{
	.banner
	{
		margin-bottom: 30px;
	}
}
@media(max-width:576px)
{
	.banner
	{
		margin-bottom: 20px;
		&__wrapper
		{
			border-radius: 5px;
			box-shadow:0 0 25px 0 rgba(0, 0, 0, 0.25);
		}
	}
}