.socials
{
	margin-bottom: 100px;
	position: relative;

	&__item
	{
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		z-index: 2;
		position: relative;
		background-color: #fff;
    	box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    	padding: 30px;
    	border-radius: 20px;
	}
	h3
	{
		font-size: 24px;
		font-weight: 300;
	}
	ul
	{
		display: flex;
		justify-content: center;
		align-items: center;
		li
		{
			margin: 0 20px;
			a
			{
				font-size: 35px;
				line-height: 35px;
				transition: 0.3s;
			
			}
		}
	}
}