.data-table
{
    margin-bottom: 15px;
    &__header
    {
        display: flex;
        justify-content: space-between;
        align-items: center;
        input
        {
            line-height: 33px;
            height: 35px;
            border: 1px solid #eee;
            border-radius: 5px;
            padding: 0 10px;
            background-color: transparent;
            &:focus,&:active
            {
                outline: none;
            }
        }
        select
        {
            line-height: 33px;
            height: 35px;
            border: 1px solid #eee;
            border-radius: 5px;
            padding: 0 10px;
            background-color: transparent;
            color: #a5a5a5;
            &:focus,&:active
            {
                outline: none;
            }
        }
    }
    &__footer
    {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .dataTables
        {
            &_paginate
            {
                display: flex;
                align-items: center;
                >a
                {
                    cursor: pointer;
                    &:focus,&:active
                    {
                        outline: none;
                    }
                }
                >span
                {
                    display: flex;
                    align-items: center;
                    margin: 0 5px;
                    &:focus,&:active
                    {
                        outline: none;
                    }
                    a
                    {
                        width: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        line-height: 28px;
                        cursor: pointer;
                        border: 1px solid #eee;
                        border-radius: 3px;
                        margin: 0 2px;
                        &:focus,&:active
                        {
                            outline: none;
                        }
                        &.current
                        {
                            background-color: #eee;
                        }
                    }
                }
            }
        }
    }
}
