.page-number
{
	padding: 30px 0;
	display: flex;
	align-items: center;
	justify-content: center;
	ul
	{
		display: flex;
		align-items: center;
		justify-content: center;
		li
		{
			margin: 0 5px;
			a
			{
				line-height: 40px;
				border-radius: 3px;
				min-width: 40px;
				padding: 0 5px;
				text-align: center;
				font-size: 18px;
				background-color: rgba(228, 202, 184, 0.2);
				display: flex;
				align-items: center;
				justify-content: center;
				color: $color2;
				transition: 0.3s;
				i
				{
					line-height: inherit;
					font-size: 12px;
				}
				&:hover
				{
					background-color: rgba(228, 202, 184, 0.3);
				}
				&:active
				{
					background-color: rgba(228, 202, 184, 0.6);
				}
			}
			span
			{
				line-height: 40px;
				border-radius: 3px;
				min-width: 40px;
				padding: 0 5px;
				text-align: center;
				font-size: 18px;
				background-color: $color2;
				display: flex;
				align-items: center;
				justify-content: center;
				color: #fff;
			}
		}
	}
}

.pagination
{
	padding: 30px 0;
	display: flex;
	align-items: center;
	justify-content: center;
	li
	{
		margin: 0 5px;
		a
		{
			line-height: 40px;
			border-radius: 3px;
			min-width: 40px;
			padding: 0 5px;
			text-align: center;
			font-size: 18px;
			background-color: rgba(228, 202, 184, 0.2);
			display: flex;
			align-items: center;
			justify-content: center;
			color: $color2;
			transition: 0.3s;
			i
			{
				line-height: inherit;
				font-size: 12px;
			}
			&:hover
			{
				background-color: rgba(228, 202, 184, 0.3);
			}
			&:active
			{
				background-color: rgba(228, 202, 184, 0.6);
			}
		}
		&.active
		{
			a
			{
				color: #fff;
			background-color: $color2;
			}
		}
	}
}
@media(max-width:480px)
{
	.page-number
	{
		ul
		{
			li{
				margin: 0 3px;
				a
				{
					min-width: 30px;
					line-height: 30px;
				
				}
				span
				{
					min-width: 30px;
				line-height: 30px;
				}
			}
		}
	}
}
@media(max-width:480px)
{
	.page-number
	{
		padding: 15px 0;
		
	}
}