.single-blog {
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 10px;

        &__title {
            h2 {
                line-height: 40px;
                font-weight: 600;
                font-size: 16px;
                color: #222;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;

            }
        }

        &__data {
            time {
                font-size: 12px;
            }
        }
    }

    &__image {
        flex: 0 0 100%;
        max-width: 100%;
        border-radius: 15px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        margin-bottom: 10px;
        height: 400px;

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            border-radius: 15px;
        }
    }

    &__like {
        margin-bottom: 20px;
        display: flex;
        align-items: center;

        &__info {

            display: flex;
            margin-right: auto;

            ul {
                display: flex;

                li {
                    font-size: 14px;
                    font-weight: 300;

                    &:first-child {
                        padding-left: 20px;
                    }

                    a {
                        font-size: 14px;
                        font-weight: 300;
                        display: flex;
                        align-items: center;
                        flex-direction: row-reverse;
                    }

                    i {
                        font-size: 15px;
                        margin-right: 5px;
                    }
                }
            }

        }

        &__author {
            padding: 10px 0;
            display: flex;
            align-items: center;


            &__avatar {

                padding-left: 10px;

                img {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                }

            }

            &__name {

                font-weight: 600;
                line-height: 30px;
                display: flex;
                align-items: center;
                font-size: 13px;

                p {
                    margin-bottom: 0;
                }

            }
        }
    }

    &__content {
        line-height: 45px;
        font-size: 15px;
        text-align: justify;
    }
}

@media(max-width:576px) {
    .single-blog {
        &__header {
            display: block;
        }

        &__image {
            height: 250px;
        }
    }
}